<template>
  <div class="flex items-center min-h-screen bg-white dark:bg-gray-800">
    <div class="container mx-auto">
      <div class="max-w-md mx-auto my-10 border p-5 rounded-md shadow-sm dark:border-gray-700">
        <div class="text-center">
          <h1 class="my-3 text-3xl font-semibold text-gray-700 dark:text-gray-200">
            Contact Me
          </h1>
          <p class="text-gray-400 dark:text-gray-400">
            I would love to hear from you, so whether you need a quote, have a question or just want to say hello, please feel free to drop me a line.
          </p>
        </div>
        <div class="m-7">
          <form action="#" method="post" @submit.prevent="sendForm" id="form" novalidate>
            <input type="checkbox" name="botcheck" v-model="form.botcheck" style="display: none"/>
            <div class="mb-6">
              <label for="name" class="block mb-2 text-sm text-gray-600 dark:text-gray-400">
                Name<sup class=" text-red-500">*</sup>
              </label>
              <input autofocus type="text" name="name" v-model="form.name" id="name" placeholder="Your Name" required class=" w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500"/>
            </div>
            <div class="mb-6">
              <label for="email" class="block mb-2 text-sm text-gray-600 dark:text-gray-400">
                Email Address<sup class=" text-red-500">*</sup>
              </label>
              <input type="email" name="email" v-model="form.email" id="email" placeholder="you@company.com" required class=" w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500"/>
            </div>
            <div class="mb-6">
              <label for="phone" class="text-sm text-gray-600 dark:text-gray-400">
                Phone Number
              </label>
              <input type="text" name="phone" v-model="form.phone" id="phone" placeholder="+62-123-4567-8910" required class=" w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500"/>
            </div>
            <div class="mb-6">
              <label for="message" class="block mb-2 text-sm text-gray-600 dark:text-gray-400">
                Message<sup class=" text-red-500">*</sup>
              </label>
              <textarea rows="5" name="message" v-model="form.message" id="message" placeholder="Your Message" class=" w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500" required></textarea>
            </div>
            <div class="mb-6">
              <button type="submit" class=" w-full px-3 py-4 text-indigo-50 bg-indigo-500 rounded-md transtition duration-200 hover:bg-indigo-600 focus:bg-indigo-700 focus:outline-none">
                <template v-if="!loadingState">
                  Send Message
                </template>
                <template v-if="loadingState">
                  <svg class="h-6 w-6" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin: auto; background: none; display: block; shape-rendering: auto;" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                    <g>
                      <circle cx="60" cy="50" r="4" fill="#a5b4fc">
                        <animate attributeName="cx" repeatCount="indefinite" dur="1.0204081632653061s" values="95;35" keyTimes="0;1" begin="-0.6566000000000001s"></animate>
                        <animate attributeName="fill-opacity" repeatCount="indefinite" dur="1.0204081632653061s" values="0;1;1" keyTimes="0;0.2;1" begin="-0.6566000000000001s"></animate>
                      </circle>
                      <circle cx="60" cy="50" r="4" fill="#a5b4fc">
                        <animate attributeName="cx" repeatCount="indefinite" dur="1.0204081632653061s" values="95;35" keyTimes="0;1" begin="-0.3234s"></animate>
                        <animate attributeName="fill-opacity" repeatCount="indefinite" dur="1.0204081632653061s" values="0;1;1" keyTimes="0;0.2;1" begin="-0.3234s"></animate>
                      </circle>
                      <circle cx="60" cy="50" r="4" fill="#a5b4fc">
                        <animate attributeName="cx" repeatCount="indefinite" dur="1.0204081632653061s" values="95;35" keyTimes="0;1" begin="0s"></animate>
                        <animate attributeName="fill-opacity" repeatCount="indefinite" dur="1.0204081632653061s" values="0;1;1" keyTimes="0;0.2;1" begin="0s"></animate>
                      </circle>
                    </g><g transform="translate(-15 0)">
                      <path d="M50 50L20 50A30 30 0 0 0 80 50Z" fill="#e0e7ff" transform="rotate(90 50 50)"></path>
                      <path d="M50 50L20 50A30 30 0 0 0 80 50Z" fill="#e0e7ff">
                        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1.0204081632653061s" values="0 50 50;45 50 50;0 50 50" keyTimes="0;0.5;1"></animateTransform>
                      </path>
                      <path d="M50 50L20 50A30 30 0 0 1 80 50Z" fill="#e0e7ff">
                        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1.0204081632653061s" values="0 50 50;-45 50 50;0 50 50" keyTimes="0;0.5;1"></animateTransform>
                      </path>
                    </g>
                  </svg>
                </template>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useMeta } from 'vue-meta';
export default {
  setup () {
    useMeta({ title: 'Contact' })
  },
  data() {
    return {
      form: {
        apikey: process.env.VUE_APP_WEB3_FORM,
        subject: "Contact Form Question",
        from_name: "PortfolioKu",
        botcheck: false,
        name: "",
        phone: "",
        email: "",
        message: "",
      },
      loadingState: false,
    };
  },
  methods: {
    async sendForm() {
      const regexEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      if (!this.form.name || !this.form.email || !this.form.message) {
        this.$toast.open({
          message: "all fields marked with * are required",
          type: "error",
          duration: 3000,
          queue: true,
          dismissible: true,
        });
        return false;
      }
      if(this.form.email.search(regexEmail) == -1){
        this.$toast.open({
          message: "your email address is not valid",
          type: "error",
          duration: 3000,
          queue: true,
          dismissible: true,
        });
        return false;
      }
      this.loadingState = true
      this.$Progress.start()
      let json = JSON.stringify(this.form);
      await fetch("https://api.web3forms.com/submit", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: json,
      })
        .then(async (response) => {
          let json = await response.json();
          if (response.status === 200) {
            this.$toast.open({
              message: json.message,
              type: "success",
              duration: 3000,
              dismissible: true,
            });
            this.form.name = ''
            this.form.email = ''
            this.form.phone = ''
            this.form.message = ''
            this.$Progress.finish()
          } else {
            this.$toast.open({
              message: json.message,
              type: "error",
              duration: 3000,
              dismissible: true,
            });
            this.$Progress.fail()
          }
          this.loadingState = false
        })
        .catch(() => {
          this.$toast.open({
            message: "Something went wrong",
            type: "error",
            duration: 3000,
            dismissible: true,
          });
          this.loadingState = false
          this.$Progress.fail()
        });
    },
  },
};
</script>

